  height: 408px;
  padding-top: 32px;
  display: flex;
}

.card__container {
  padding: 0 16px 16px;
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.card__img img {
  width: 100%;
  height: 100%;
}

.card__title {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

